h1 {
	color: white;
}

.generalStatsPage > div:first-child {
	display: flex;
	align-items: center;
	justify-content: center;
}

.generalStatsPage > div:first-child > h1 {
	text-align: center;
	margin-left: 1em;
}

.submitBtn {
	background-color: #00000050;
}

.cards {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 0.5em;
	margin: 1em;
	margin-bottom: 0;
	padding-bottom: 1em;
}

.cards > * {
	background-color: #00000050;
	height: 7.5em;
	padding: 1em;
	border-radius: 1em;
	-webkit-border-radius: 1em;
	-moz-border-radius: 1em;
	-ms-border-radius: 1em;
	-o-border-radius: 1em;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.stat {
	color: white;
}

.value {
	color: #bbbbbb;
}

.reload {
	color: white;
	font-size: 3em;
}